.buttono {
    width: 49.25%;
    background: #425062;
    color: #fff;
    padding: 20px;
    display: inline-block;
    font-size: 25px;
    text-align: center;
    vertical-align: middle;
    margin-right: 0.5px;
    margin-left: 0.5px;
    border-right: solid 2px #3C4857;
    border-bottom: solid 2px #3C4857;
    transition: all 0.2s ease-in-out;
}
  

.buttono:hover {
  background: #E0B612;
  transform: rotate(5deg);
}
  
