.Area {
font-family: Roboto, RobotoDraft, Helvetica, Arial, sans-serif; 
font-size: 16px; 
background: black; 
color: white;
border-style: solid;
border-width: 1px;
border-radius: 1px;
border-color: #FFD700;
}

.container {
display: grid;
grid-template-columns: 50% 50%;
width: 100%;
}

.left {
width: 100%;
height: 98vh;
}

.right {
width: 100%;
height: 97.5vh;
overflow: auto;
font-family: 'Georgia';
background: #0f3854;
background: radial-gradient(ellipse at center,  #0a2e38  0%, #000000 70%);
}
#preview{
color: white;
margin-left: 5px;

}
#editor {
width: 100%;
height: 100%;
outline: none;
padding: .25vw;
}

code {
color: #fbfdfd;
padding: .25vw .5vw;
}